.container {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 98vh;
}
.weather-box {
  background: linear-gradient(#009dff 0%, #870fff 100%);
  /* background: linear-gradient(#000428 0%, #004e92 100%); */

  width: 340px;
  height: 480px;
  border-radius: 6px;
  padding: 20px;
  margin: 10px;
}
.search-bar {
  display: flex;
  align-items: center;
  gap: 14px;
  justify-content: center;
  margin-top: 20px;
}
.search-bar input {
  height: 38px;
  width: 200px;
  border-radius: 20px;
  border: none;
  outline: none;
  padding-left: 10px;
  color: #000;
  font-size: 15px;
}
.search-bar img {
  width: 35px;
  height: 35px;
  line-height: 32px;
  padding: 8px;
  background-color: #fff;
  color: #870fff;
  border-radius: 50%;
  cursor: pointer;
}
.w-status {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-top: 20px;
}
.w-status img {
  width: 140px;
}
.weather-temp {
  color: #fff;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 70px;
  margin-bottom: 10px;
}
.weather-location {
  color: #fff;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 35px;
}
.weather-info {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 20px 5%;
  margin-top: 15px;
  margin-bottom: 20px;
}
.weather-info .humidity-info {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 8px;
}

.humidity-info div {
  display: flex;
  /* align-items: center;
  justify-content: flex-start; */
  flex-direction: column;
  color: #fff;
}
.humidity-info img {
  width: 30px;
}
.humidity-info h2 {
  font-size: 22px;
}
.wind-info h2 {
  font-size: 22px;
}
.wind-info div {
  display: flex;
  /* align-items: center;
  justify-content: flex-start; */
  flex-direction: column;
  color: #fff;
  text-align: start;
}
.wind-info {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 8px;
}
.wind-info img {
  width: 35px;
}
